.hero-img {
  background: rgba(0, 0, 0, .3);
  height: 58vh;
  width: 100%;
  position: relative;

  &:before {
    content: "";
    background: url('../assets/3.jpg') no-repeat center center/cover;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
  }

  .heading {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  h1{
    font-size: 2.4rem;
  }
  p{
    font-size: 1.4rem;
  }

}
@media screen and (max-width:640px){
  .hero-img{
    h1{
      font-size: 2rem;
    }
  }
}